.page-home {
  display: flex;
  flex-direction: column;
}
.page-home .header {
  height: 44px;
  padding: 10px 20px;
  background-color: #d43d3d;
  display: flex;
  justify-content: space-between;
}
.page-home .header .hd-l {
  width: 40px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
}
.page-home .header .hd-m {
  width: 100px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: cover;
}
.page-home .header .hd-r {
  width: 40px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
}
.page-home .navbar {
  height: 37px;
  background-color: #f4f5f6;
  border-bottom: 1px solid rgba(221, 221, 221, 0.4);
  display: flex;
}
.page-home .navbar .navlist {
  flex: 1 1;
  line-height: 36px;
  overflow: auto;
  overflow-y: hidden;
  white-space: nowrap;
}
.page-home .navbar .navlist .nav {
  display: inline-block;
  padding: 0 14px;
  font-size: 17px;
  color: #505050;
}
.page-home .navbar .navlist .nav.active {
  color: #f85959;
}
.page-home .navbar .plusnav {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  box-shadow: -4px 0 5px -3px #aaa;
}
.page-home .navbar .plusnav::before,
.page-home .navbar .plusnav::after {
  content: " ";
  display: block;
  position: absolute;
  background-color: #f85959;
}
.page-home .navbar .plusnav::before {
  width: 17px;
  height: 2px;
}
.page-home .navbar .plusnav::after {
  width: 2px;
  height: 17px;
}
.page-home .feedlist {
  flex: 1 1;
  overflow: auto;
  overflow-x: hidden;
}
.page-home .feedlist .feed {
  display: block;
  margin: 0 15px;
  padding: 16px 0;
  border-bottom: 1px solid rgba(221, 221, 221, 0.4);
}
.page-home .feedlist .feed:last-of-type {
  border-bottom-color: transparent;
}
.page-home .feedlist .feed .feed-tit {
  font-size: 17px;
  line-height: 1.4;
  color: #222;
}
.page-home .feedlist .feed .feed-info {
  margin-top: 6px;
  font-size: 12px;
  color: #999;
}
.page-home .feedlist .feed .feed-info .fixtag {
  display: inline-block;
  border: 1px solid rgba(153, 153, 153, 0.5);
  border-radius: 2px;
  height: 16px;
  line-height: 16px;
  font-size: 10px;
  padding: 0 2px;
  margin-right: 4px;
}
.page-home .feedlist .feed .feed-info .fixtag.fixtop {
  color: #f85959;
  border-color: rgba(248, 89, 89, 0.5);
}
.page-home .feedlist .feed .feed-info .close {
  position: relative;
  float: right;
  box-shadow: 0 0 1px #999;
  border-radius: 2px;
  margin-top: 3px;
  height: 12px;
  width: 20px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 8px;
}
.page-home .feedlist .feed-smallpic .feed-l {
  display: inline-block;
  vertical-align: middle;
  width: 67%;
  padding-right: 12px;
}
.page-home .feedlist .feed-smallpic .feed-r {
  display: inline-block;
  vertical-align: middle;
  width: 33%;
  overflow: hidden;
}
.page-home .feedlist .feed-smallpic .feed-r img {
  float: left;
  width: 100%;
}
.page-home .feedlist .feed-threepic .feed-pic {
  margin-top: 4px;
  overflow: hidden;
}
.page-home .feedlist .feed-threepic .feed-pic img {
  float: left;
  width: 33%;
}
.page-home .feedlist .feed-threepic .feed-pic img:nth-of-type(2) {
  margin: 0 0.5%;
}
.page-home .feedlist .feed-bigpic .feed-pic {
  margin-top: 4px;
  overflow: hidden;
}
.page-home .feedlist .feed-bigpic .feed-pic img {
  float: left;
  width: 100%;
}
.page-home .feedlist .feed-none {
  text-align: center;
  font-size: 12px;
  color: #999;
  padding: 10px 0;
}
